import React, { useState, useEffect } from "react";
import { BtnPrimary, BtnPrimaryNav, BtnSecondary, BtnSecondaryNav, BtnIconOutline } from "../components/Buttons"
import { ContainerNav } from "../components/Containers";
import logo from "../assets/logo_with_name.svg"
import burgerMenu from "../assets/images/Burger menu.svg"
import close from "../assets/images/Close.svg"


const Navbar = () => {

  const [hideNavbar, setHideNavbar] = useState("");
  const [scrollPosition, setScrollPosition] = useState(0);
  const [nav, setNav] = useState(false);

  const openNav = (navState) => {
    setNav(!navState);
    console.log("clicked");
  }

  let lastScrollTop = window.scrollY;
  
  const handleScroll = () => {
    const position = window.pageYOffset;

    setScrollPosition(position);
    if (position > lastScrollTop) {
      setHideNavbar("-mt-24");
    } else {
      setHideNavbar("-mt-0");
    }
    lastScrollTop = position;
  }

  useEffect(() => {
    window.addEventListener("scroll", handleScroll, {passive: true});
    
    return () => {
      window.addEventListener("scroll", handleScroll);
    };
  }, []);

  if (scrollPosition > 1) {
    console.log(scrollPosition);
  }

  return (
    <>
    {nav && <div className="fixed h-screen w-screen bg-white z-40">
        <ul className="flex flex-col h-full justify-center items-center" >
          <li className="px-4 text-xl font-semibold mb-4">Como funciona</li>
          <li className="px-4 text-xl font-semibold mb-4">Nuestros servicios</li>
          <li className=" w-64 mb-4"><BtnSecondary href="#">Iniciar sesion</BtnSecondary></li>
          <li className=" w-64 mb-4"><BtnPrimary href="#">Empieza ahora</BtnPrimary></li>
        </ul>
      </div>}
      <ContainerNav styleOut={`fixed w-screen z-40 transition-all duration-1000
      ${hideNavbar}
      ${(scrollPosition > 80) ? "bg-white" : "bg-white-0"}
      `} style="items-center justify-between">
        <img src={logo} alt="logo selia" className="  w-28 ts:w-22" />
        <ul className="flex flex-row gap-4 justify-end items-center ts:hidden" >
          <li className="px-4">Como funciona</li>
          <li className="px-4">Nuestros servicios</li>
          <li><BtnSecondaryNav href="#">Iniciar sesion</BtnSecondaryNav></li>
          <li><BtnPrimaryNav href="#">Empieza ahora</BtnPrimaryNav></li>
        </ul>
        <BtnIconOutline href="#" style={"hidden ts:block p-2"} > <img onClick={() => openNav(nav)} src={ nav ? close : burgerMenu} alt="burger menu" /> </BtnIconOutline>
      </ContainerNav>
    </>
  );
}

export default Navbar;