import React from "react";
import { motion } from "framer-motion";

export const ContainerL = ({ children, style, styleOut }) => {

    return (
        <motion.div className={`
        ${styleOut}
        flex justify-center
        tl:px-12 ml:px-4
        `}

        >
            <div className={`
             ${style}
             max-w-cl w-full flex py-6
             `}>
                {children}
            </div>
        </motion.div>
    );
}

export const ContainerS = ({ children, style, styleOut }) => {
    return (
        <div className={`
        ${styleOut}
        flex justify-center py-6
        tl:px-12 ml:px-4
        `} >
            <div className={`
             ${style}
             max-w-cs w-full flex
             `}>
                {children}
            </div>
        </div>
    );
}

export const ContainerNav = ({ children, style, styleOut }) => {
    return (
        <div className={`
        ${styleOut}
        flex justify-center
        tl:px-12 ml:px-4
        `} >
            <div className={`
             ${style}
             max-w-cl w-full flex py-6 ts:py-3
             `}>
                {children}
            </div>
        </div>
    );
}
