import React from "react";
import { BtnPrimary } from "../components/Buttons"
import { H1 } from "../components/Headings";
import { ContainerL } from "../components/Containers";
import { motion } from "framer-motion";
// import { Frame, useMotionValue, useTransform } from "framer-motion";
import heroImage from "../assets/images/home-banner.png"
import arrow from "../assets/images/Arrow.svg"

const HeroHome = () => {
    return (
        <>
            <ContainerL style="text-left gap-4 items-center mt-20
            ml:flex-col-reverse ml:text-center
            ">
                <motion.div className="w-full"
                    animate={{
                        x: 0,
                        opacity: 1
                    }}  
                    initial={{
                        x: -200,
                        opacity: 0.1
                    }}  
                    transition={{
                        type: "tween",
                        duration: 1,
                        ease: "circOut"
                    }} 
                >
                    <H1>Empieza terapia<br />
                        en línea hoy</H1>
                    <p className="text-xl my-10
                    tl:text-lg tl:my-6
                    ml:my-4
                    ms:text-tiny
                    ">
                        Encuentra tu psicólogo ideal e inicia <br />
                        tu camino de bienestar desde cualquier lugar</p>
                    <BtnPrimary />
                </motion.div>
                <div className="w-full
                ml:max-w-xxs
                "
                >
                    <motion.img src={heroImage} alt="hero image"
                    animate={{ x: 100 }}
                    animate={{
                        x: 0,
                        opacity: 1
                    }}  
                    initial={{
                        x: 200,
                        opacity: 0.1
                    }}  
                    transition={{
                        type: "tween",
                        duration: 1,
                        ease: "circOut"
                    }}  
                    />
                </div>
            </ContainerL>
            <img src={arrow} alt="flechita" className="m-auto" />
        </>
    );
}

export default HeroHome;
